import { getDefaultOptions, request } from 'api/helpers';

const resource = 'api/d-3-points';

export const apiD3pointGet = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiD3pointPost = async (serviceUrl, d3point) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: d3point ? JSON.stringify(d3point) : null,
  };
  return request(url, options);
};

export const apiD3pointPut = async (serviceUrl, id, d3point) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: d3point ? JSON.stringify(d3point) : null,
  };
  return request(url, options);
};

export const apiD3pointDelete = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options);
};
